import Vue from 'vue'
import Router from 'vue-router'
// import Home from './views/Home.vue'
import Main from './components/pages/Main.vue'
import About from './components/pages/About.vue'
import Shopping_cart from './components/pages/Shopping_cart.vue'
import Login from './components/pages/Login.vue'
import Business from './components/pages/Business.vue'
import Contact from './components/pages/Contact.vue'

import Products from './components/product/Products.vue'
import Products_Grid_List from './components/product/Products_Grid_List.vue'
import Products_page from './components/product/Products_page.vue'

import Home from './components/Home.vue'

import Personal from './components/personal/Personal.vue'
import Edit_details from './components/personal/Edit_details.vue'
import Delivery_addresses from './components/personal/Delivery_addresses.vue'

import Admin from './components/backstage/Admin.vue'
import Backstage from './components/backstage/Backstage.vue'
import User from './components/backstage/Users.vue'
import Product from './components/backstage/Product.vue'

import error from './components/Error.vue'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [{
      path: '*',
      component: error
    }, {
      path: '/',
      name: '',
      component: Home,
      children: [{
        path: '',
        name: '首頁',
        component: Main
      }]
    },
    {
      path: '/login',
      name: '登入',
      component: Login
    },
    {
      path: '/business',
      name: '商業註冊',
      component: Business
    }, {
      path: '/shopping_cart',
      name: '購物車',
      component: Shopping_cart
    }, {
      path: '/contact',
      name: '聯繫我們',
      component: Contact
    }, {
      path: '/about',
      name: '關於我們',
      component: About
    },
    {
      path: '/personal',
      component: Personal,
      children: [{
        path: '/',
        component: Edit_details,
        meta: {
          requiresAuth: true
        },
      }, {
        path: '/personal/Delivery_addresses',
        component: Delivery_addresses,
        meta: {
          requiresAuth: true
        },
      }]
    },
    {
      path: '/products',
      component: Products,
      children: [{
        path: '/',
        component: Products_Grid_List
      }, {
        path: '/products/Products_page/:id',
        component: Products_page
      }]
    },
    {
      path: '/backstage',
      name: '',
      component: Backstage,
      children: [{
        path: '/backstage/users',
        name: 'User',
        component: User
      }, {
        path: '/backstage/product',
        name: 'Product',
        component: Product
      }]
    },
    {
      path: '/admin',
      name: 'Admin',
      component: Admin
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import( /* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
