<template>
  <div>
    <Header/>
    <section class="container mt-4">
      <h1 class="mt-5 border-bottom pb-3">Shopping Cart</h1>
      <el-table class="my-3" :data="cartProductsList" style="width: 100%">
        <el-table-column prop="image" :label="$t('cart_table.images')" width="200">
          <template slot-scope="scope">
            <img :src="scope.row.img" min-width="100" height="100">
          </template>
        </el-table-column>
        <el-table-column
          v-if="getLang === 'en'"
          prop="enname"
          :label="$t('cart_table.product')"
          width="300"
        ></el-table-column>
        <el-table-column
          v-if="getLang === 'zh'"
          prop="name"
          :label="$t('cart_table.product')"
          width="300"
        ></el-table-column>
        <el-table-column prop="price" :label="$t('cart_table.unit')" width="100"></el-table-column>
        <el-table-column prop="count" :label="$t('cart_table.qty')" width="200">
          <template slot-scope="scope">
            <el-input-number
              size="mini"
              :min="1"
              :value="scope.row.num"
              v-on:input="handleBlur"
              @change="handleChange( scope.row )"
            ></el-input-number>
          </template>
        </el-table-column>
        <el-table-column prop="total_num" :label="$t('cart_table.subtotal')" width="150"></el-table-column>
        <el-table-column :label="$t('cart_table.delete')" width="150">
          <template slot-scope="scope">
            <el-button
              type="danger"
              plain
              icon="el-icon-delete"
              size="mini"
              @click="dialogVisibleTrue( scope.row )"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="row my-3">
        <div class="col-md-4 my-3">
          <div class="p-3" style="background: #f5f7f9;">
            <h4 class="mb-3">Estimate Shipping and Tax</h4>
            <p>Enter your destination to get a shipping estimate.</p>
            <label for="Country">{{$t('delivery.country')}}</label>
            <select class="custom-select mr-sm-2" id="Country">
              <option selected value="0">Australia</option>
            </select>
            <label class="mt-3" for="state">State/Province:</label>
            <select class="custom-select mr-sm-2" id="state">
              <option selected value="0">Please select region, state or province</option>
            </select>
            <label class="mt-3" for="Zip">Zip/Postal Code</label>
            <input type="tel" class="form-control" id="Zip" placeholder="ex: 2000">
            <button class="btn mt-3 primary-btn">Get a Quote</button>
          </div>
        </div>
        <div class="col-md-4 my-3">
          <div class="p-3" style="background: #f5f7f9;">
            <h4 class="mb-3">Discount Codes</h4>
            <p>Enter your coupon code if you have one.</p>
            <input type="text" class="form-control" id="United" placeholder="United States">
            <button class="btn mt-3 primary-btn">Apply Coupon</button>
          </div>
        </div>
        <div class="col-md-4 my-3">
          <div class="p-3" style="background: #f5f7f9; min-height: 205px">
            <div>
              <h3>Quantity：{{totalNum}}</h3>
            </div>
            <div class="my-3">
              <h3>Grand Total：{{totalPrice}}</h3>
            </div>
            <button class="btn mt-3 primary-btn">CHECKOUT
              <font-awesome-icon icon="angle-right" class="ml-3"/>
            </button>
          </div>
        </div>
      </div>
    </section>
    <Footer/>
  </div>
</template>

<script>
import Header from "../Header.vue";
import Footer from "../Footer.vue";
import { mapState, mapGetters, mapActions } from "vuex";
export default {
  name: "Shopping_cart",
  metaInfo: {
    meta: [
      {
        property: "og:title",
        content: "Greenvalley"
      },
      { property: "og:description", content: "Test 章魚" },
      { property: "og:type", content: "Test website" },
      { property: "og:url", content: "localhost" },
      {
        property: "og:image",
        content: ""
      }
    ],
    title: "Greenvalley Cart",
    // override the parent template and just use the above title only
    titleTemplate: null
  },
  components: {
    Header,
    Footer
  },
  data() {
    return {
      dialogVisible: false,
      result: null,
      input_number_value: 1
    };
  },
  computed: {
    ...mapGetters([
      "cartProducts",
      "totalNum",
      "totalPrice",
      "cartProductsList",
      "getLang"
    ])
  },
  methods: {
    ...mapActions(["delProduct", "numChange"]),
    dialogVisibleTrue(data) {
      this.dialogVisible = true;
      this.result = data;
      this.delProduct(this.result);
      this.dialogVisible = false;
    },
    handleBlur(value) {
      this.input_number_value = value;
    },
    handleChange(data) {
      console.log(data);
      data.value = this.input_number_value;
      this.numChange(data);
    }
  }
};
</script>
