<template>
  <header>
    <div class="bg-light">
      <nav class="container navbar-md navbar-light d-flex justify-content-end language py-0">
        <div class="d-flex justify-content-end d-block">
          <ul class="list-inline mb-0">
            <li class="list-inline-item">
              <a
                class="navbar-brand mr-3 text-center"
                :class="{'router-link-active':lang}"
                href="#"
                @click="english"
              >English</a>
            </li>
            <li class="list-inline-item">
              <a
                class="navbar-brand mr-0 text-center"
                :class="{'router-link-active':!lang}"
                href="#"
                @click="chinese"
              >中文</a>
            </li>
          </ul>
        </div>
      </nav>
    </div>

    <div class="container my-5">
      <div class="row h-100">
        <div class="col-md-3">
          <img src="../static/images/logo.png" alt>
          <router-link
            v-if="totalNumList!=0"
            class="nav-link text-secondary Mobile d-none"
            :to="{path: '/shopping_cart'}"
          >
            <el-badge :value="totalNumList" class="item">
              <font-awesome-icon icon="cart-arrow-down" class="mr-2"/>
            </el-badge>
          </router-link>
        </div>
        <div class="col-md-9 private">
          <ul class="nav nav-pills d-flex justify-content-end">
            <li class="nav-item">
              <router-link class="nav-link text-secondary" :to="{path: '/personal'}">
                <font-awesome-icon icon="money-check-alt" class="mr-2"/>
                {{ $t("header.account") }}
              </router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link text-secondary" :to="{path: '/login'}" v-if="islogin">
                <font-awesome-icon icon="sign-in-alt" class="mr-2"/>
                {{ $t("header.login") }}
              </router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link text-secondary" :to="{path: '/Business'}" v-if="islogin">
                <font-awesome-icon icon="user-plus" class="mr-2"/>
                {{ $t("header.SignUp") }}
              </router-link>
            </li>
            <li class="nav-item">
              <a class="nav-link text-secondary" v-if="!islogin" @click.prevent="logout">
                <font-awesome-icon icon="sign-out-alt" class="mr-2"/>
                {{ $t("header.signout") }}
              </a>
            </li>
            <li class="nav-item" v-if="totalNumList==0">
              <router-link class="nav-link text-secondary" :to="{path: '/shopping_cart'}">
                <font-awesome-icon icon="cart-arrow-down" class="mr-2"/>
                {{ $t("header.cart") }}
              </router-link>
            </li>
            <li class="nav-item" v-else="totalNumList!=0">
              <router-link class="nav-link text-secondary" :to="{path: '/shopping_cart'}">
                <el-badge :value="totalNumList" class="item">
                  <font-awesome-icon icon="cart-arrow-down" class="mr-2"/>
                  {{ $t("header.cart") }}
                </el-badge>
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="container">
      <nav class="navbar navbar-expand-lg navbar-light bg-light home-menu pr-0">
        <a class="navbar-brand">Greenvalley</a>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <ul class="navbar-nav d-flex justify-content-around w-100">
            <li class="nav-item w-100 text-center">
              <router-link class="nav-link" to="/" exact>{{ $t("header_menu.home") }}</router-link>
            </li>
            <li class="nav-item w-100 text-center">
              <router-link class="nav-link" to="/products">{{ $t("header_menu.products") }}</router-link>
            </li>
            <li class="nav-item w-100 text-center">
              <router-link class="nav-link" to="/about">{{ $t("header_menu.about") }}</router-link>
            </li>
            <li class="nav-item w-100 text-center">
              <router-link
                class="nav-link"
                href="#"
                :to="{path: '/contact'}"
              >{{ $t("header_menu.contact") }}</router-link>
            </li>
            <li class="nav-item w-100 text-center">
              <a class="nav-link" href="#">{{ $t("header_menu.career") }}</a>
            </li>
            <li class="nav-item w-100 text-center">
              <router-link class="nav-link" :to="{path: '/backstage/users'}">後台</router-link>
            </li>
            <li class="nav-item w-100 text-center private-menu d-none">
              <router-link
                class="nav-link text-secondary"
                :to="{path: '/personal'}"
              >{{ $t("header.account") }}</router-link>
            </li>
            <li class="nav-item w-100 text-center private-menu d-none">
              <router-link
                class="nav-link text-secondary"
                :to="{path: '/login'}"
                v-if="islogin"
              >{{ $t("header.login") }}</router-link>
            </li>
            <li class="nav-item w-100 text-center private-menu d-none">
              <router-link
                class="nav-link text-secondary"
                :to="{path: '/Business'}"
                v-if="islogin"
              >{{ $t("header.SignUp") }}</router-link>
            </li>
            <li class="nav-item w-100 text-center private-menu d-none">
              <a
                @click.prevent="logout"
                class="nav-link text-secondary"
                v-if="!islogin"
              >{{ $t("header.signout") }}</a>
            </li>
          </ul>
        </div>
      </nav>
    </div>
  </header>
</template>

<script>
import en from "vee-validate/dist/locale/en";
import zh from "vee-validate/dist/locale/zh_CN";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Header",
  props: {
    msg: String
  },
  data() {
    return {
      islogin: true,
      lang: true
    };
  },
  methods: {
    ...mapActions(["updateLang"]),
    //切換英文語言包
    english() {
      this.$i18n.locale = "en";
      this.$validator.localize("en", en);
      this.updateLang("en");
      this.lang = !this.lang;
    },
    //切換中文語言包
    chinese() {
      this.$i18n.locale = "zh";
      this.$validator.localize("zh_CN", zh);
      this.updateLang("zh");
      this.lang = !this.lang;
    },
    //登出方法
    logout() {
      const api = `http://api.localhost/api/auth/logout`;
      const vm = this;
      this.$http.post(api).then(response => {
        console.log(response.data);
        if (response.data.status) {
          localStorage.setItem("token", "");
          this.message("Logged out", "success");
          vm.$router.push("/login");
        }
      });
    }
  },
  computed: {
    //購物車加總
    ...mapGetters(["totalNumList"])
  },
  created() {
    let initlang = window.localStorage.getItem("UserLang");
    if (initlang === "en") {
      this.lang = true;
    } else {
      this.lang = false;
    }
    //判斷是否登入 顯示Menu
    let token = window.localStorage.getItem("token");
    if (!token || token === null) {
      this.islogin = true;
    } else {
      this.islogin = false;
    }
  }
};
</script>


