<template>
  <div>
    <h2>Product List</h2>
    <div class="row">
      <div class="col-12">
        <div v-for="item in goodList" class="card-list shadow border my-3">
          <div class="row m-0">
            <div class="col-lg-2 col-md-4 col-sm-4 col-5 px-0 border-right">
              <img v-if="item.icon==1" class="sale" src="../../static/images/sale.png" alt="Sale">
              <img v-if="item.icon==2" class="sale" src="../../static/images/new.png" alt="Sale">
              <img v-if="item.icon==3" class="sale" src="../../static/images/top.png" alt="Sale">
              <div class="p-2">
                <router-link
                  href="#"
                  class="text-center"
                  :to="{path: `/products/Products_page/${item.id}`}"
                >
                  <img class="img-fluid prev" alt="Responsive image" :src="item.img">
                </router-link>
              </div>
            </div>
            <div class="col-lg-8 col-md-5 col-sm-5 col-7 border-right p-2">
              <h6 class="my-2 text-truncate">
                <router-link
                  v-if="getLang === 'en'"
                  href="#"
                  class="text-center"
                  :to="{path: '/products/Products_page'}"
                >{{item.enname}}</router-link>
                <router-link
                  v-else="getLang === 'zh'"
                  href="#"
                  class="text-center"
                  :to="{path: '/products/Products_page'}"
                >{{item.name}}</router-link>
              </h6>
              <p class="mb-2" v-if="getLang === 'en'">{{item.content}}</p>
              <p class="mb-2" v-else="getLang === 'zh'">{{item.zhcontent}}</p>
            </div>
            <div class="col-lg-2 col-md-3 col-sm-3 col-12 add-to-cart p-1">
              <div class="text-center my-2 add-to-cart-price mr-auto">
                <strong
                  class="text-danger h2 mb-0 d-flex align-items-center justify-content-center"
                >{{ item.price | currency }}</strong>
              </div>
              <div class="text-center add-to-cart-del-price">
                <del v-if="item.delprice!=0">{{ item.delprice | currency }}</del>
              </div>
              <div class="mt-4 text-center add-to-cart-icon">
                <font-awesome-icon
                  class="love"
                  icon="heart"
                  :style="{ color: '#e74c3c' }"
                  size="2x"
                />
              </div>
              <div class="text-center cart-btn my-3">
                <button
                  class="btn primary-btn"
                  plain
                  @click="addToCart(item), messages()"
                >Add to Cart</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Products_Grid_List",
  data() {
    return {
      lang: ""
    };
  },
  computed: {
    ...mapGetters(["goodList", "getLang"])
  },
  methods: {
    ...mapActions(["addToCart"]),
    messages() {
      this.message(this.$i18n.t("messages.cart"), "success");
    }
  }
};
</script>
