<template>
  <div>
    <div class="row mt-5">
      <div class="col-md-4">
        <div class="card rounded-0 h-100">
          <div class="card-body d-flex align-items-center">
            <font-awesome-icon
              icon="users"
              class="text-secondary mr-2 d-none d-sm-block"
              size="5x"
            />
            <div class="text-center w-100">
              <div class="h5">all</div>
              <div class="display-4">
                <v-countup :start-value="start" :end-value="tableData.length"></v-countup>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div class="card rounded-0 h-100">
          <div class="card-body d-flex align-items-center">
            <font-awesome-icon icon="user" class="text-secondary mr-2 d-none d-sm-block" size="5x"/>
            <div class="text-center w-100">
              <div class="h5">general</div>
              <div class="display-4">
                <v-countup :start-value="start" :end-value="general"></v-countup>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div class="card rounded-0 h-100">
          <div class="card-body d-flex align-items-center">
            <font-awesome-icon
              icon="user-tie"
              class="text-secondary mr-2 d-none d-sm-block"
              size="5x"
            />
            <div class="text-center w-100">
              <div class="h5">business</div>
              <div class="display-4">
                <v-countup :start-value="start" :end-value="business"></v-countup>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <button class="btn primary-btn my-3" @click="export2Excel">Export</button>
    <loading :active.sync="isLoading"></loading>
    <v-client-table :data="tableData" :columns="columns" :options="options">
      <button
        slot="Edit"
        slot-scope="props"
        class="btn btn-outline-info"
        @click="edit(props.row)"
      >edit</button>
    </v-client-table>

    <div
      class="modal fade"
      id="userModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="userModal"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <div class="d-flex justify-content-center w-100">
              <h5
                class="modal-title"
                id="exampleModalLabel"
              >{{tempProduct.first_name}} {{tempProduct.last_name}}</h5>
            </div>

            <button type="button" class="close ml-0" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <h3 class="my-3">Term of Payment</h3>
            <div class="form-row">
              <div class="col-sm-6 mb-3">
                <label for="Payment">Payment</label>
                <select class="custom-select mr-sm-2" id="Payment">
                  <option selected value="0">General</option>
                  <option value="1">COD</option>
                  <option value="2">Weekly</option>
                  <option value="3">Fornightly</option>
                  <option value="4">Monthly</option>
                </select>
              </div>
              <div class="col-sm-6 mb-3">
                <label for="Credit">Credit amount</label>
                <input
                  type="text"
                  class="form-control"
                  id="Credit"
                  name="Credit"
                  v-validate="'numeric'"
                  :class="{'is-invalid': errors.has('Credit') }"
                >
                <span class="text-danger" v-if="errors.has('Credit')">{{errors.first('Credit')}}</span>
              </div>
            </div>
            <h3>Account</h3>
            <div class="form-row">
              <div class="col-sm-6 mb-3">
                <label for="first_name">First name</label>
                <font color="red">*</font>
                <input
                  type="text"
                  class="form-control"
                  id="first_name"
                  v-validate="'required'"
                  name="first"
                  :class="{'is-invalid': errors.has('first') }"
                  v-model="tempProduct.first_name"
                >
                <span class="text-danger" v-if="errors.has('first')">{{errors.first('first')}}</span>
              </div>
              <div class="col-sm-6 mb-3">
                <label for="last_name">Last name</label>
                <font color="red">*</font>
                <input
                  type="text"
                  class="form-control"
                  id="last_name"
                  name="last"
                  v-validate="'required'"
                  :class="{'is-invalid': errors.has('last') }"
                  v-model="tempProduct.last_name"
                >
                <span class="text-danger" v-if="errors.has('last')">{{errors.first('last')}}</span>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-6 mb-3">
                <label for="birth">Date of Birth</label>
                <font color="red">*</font>
                <el-date-picker
                  ref="picker"
                  :editable="true"
                  format="dd-MM-yyyy"
                  value-format="dd-MM-yyyy"
                  v-model="tempProduct.birth"
                  type="date"
                  placeholder="Date"
                  id="birth"
                  @change="dateChangebirthday"
                  class="w-100"
                  name="birth"
                  v-validate="'required'"
                ></el-date-picker>
                <span class="text-danger" v-if="errors.has('birth')">{{errors.first('birth')}}</span>
              </div>
              <div class="col-sm-6 mb-3">
                <label for="Postcode">Postcode</label>
                <font color="red">*</font>
                <input
                  type="tel"
                  class="form-control"
                  id="Postcode"
                  maxlength="4"
                  v-validate="'numeric|required|min:4'"
                  name="Postcode"
                  :class="{'is-invalid': errors.has('Postcode') }"
                  v-model="tempProduct.postcode"
                >
                <span class="text-danger" v-if="errors.has('Postcode')">{{errors.first('Postcode')}}</span>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-6 mb-3">
                <label for="email">Email address</label>
                <font color="red">*</font>
                <input
                  type="email"
                  class="form-control"
                  id="email"
                  name="email"
                  v-validate="'required|email'"
                  :class="{'is-invalid': errors.has('email') }"
                  v-model="tempProduct.email"
                >
                <span class="text-danger">{{errors.first('email')}}</span>
              </div>
              <div class="col-sm-6 mb-3">
                <label for="Mobile number">Mobile number</label>
                <font color="red">*</font>
                <input
                  type="tel"
                  class="form-control"
                  id="Mobile number"
                  name="Mobile number"
                  maxlength="12"
                  v-validate="'required|min:12'"
                  data-vv-as="field"
                  :class="{'is-invalid': errors.has('Mobile number') }"
                  v-model="tempProduct.mobile_number"
                  v-mask="'## #### ####'"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Mobile number')"
                >{{errors.first('Mobile number')}}</span>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-6 mb-3">
                <label for="password">Password</label>
                <font color="red">*</font>
                <input
                  type="text"
                  class="form-control"
                  id="password"
                  v-validate="'required|min:6|alpha_num'"
                  :class="{'is-invalid': errors.has('password') }"
                  name="password"
                  minlength="6"
                  v-model="tempProduct.password"
                >
                <span class="text-danger" v-if="errors.has('password')">{{errors.first('password')}}</span>
              </div>
              <div class="col-sm-6 p-2">
                <div class="form-row ml-0 mt-4">
                  <div class="custom-control custom-radio">
                    <input
                      type="radio"
                      id="customRadio1"
                      name="customRadio"
                      class="custom-control-input"
                      value="Male"
                      v-model="tempProduct.sex"
                    >
                    <label class="custom-control-label mr-3" for="customRadio1">Male</label>
                  </div>
                  <div class="custom-control custom-radio">
                    <input
                      type="radio"
                      id="customRadio2"
                      name="customRadio"
                      class="custom-control-input"
                      value="Female"
                      v-model="tempProduct.sex"
                    >
                    <label class="custom-control-label" for="customRadio2">Female</label>
                  </div>
                </div>
              </div>
            </div>
            <h3 class="my-3">GENERAL INFORMATION</h3>
            <div class="form-row">
              <div class="col-sm-6 mb-4">
                <label for="Registered Company Name">Registered Company Name</label>
                <font color="red">*</font>
                <input
                  type="text"
                  class="form-control"
                  id="Registered Company Name"
                  v-validate="'required'"
                  name="Registered Company Name"
                  :class="{'is-invalid': errors.has('Registered Company Name') }"
                  v-model="tempProduct.registered_company_name"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Registered Company Name')"
                >{{errors.first('Registered Company Name')}}</span>
              </div>
              <div class="col-sm-6 mb-4">
                <label for="Business Name">Business Name</label>
                <font color="red">*</font>
                <input
                  type="text"
                  class="form-control"
                  id="Business Name"
                  v-validate="'required'"
                  name="Business Name"
                  :class="{'is-invalid': errors.has('Business Name') }"
                  v-model="tempProduct.business_name"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Business Name')"
                >{{errors.first('Business Name')}}</span>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-6 col-md-4 mb-4">
                <label for="Nature of Business">Nature of Business</label>
                <font color="red">*</font>
                <input
                  type="tel"
                  class="form-control"
                  id="Nature of Business"
                  maxlength="10"
                  v-validate="'required'"
                  name="Nature of Business"
                  :class="{'is-invalid': errors.has('Nature of Business') }"
                  v-model="tempProduct.nature_business"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Nature of Business')"
                >{{errors.first('Nature of Business')}}</span>
              </div>
              <div class="col-sm-6 col-md-4 mb-4">
                <label for="Business Contact Number">Business Contact Number</label>
                <font color="red">*</font>
                <input
                  type="tel"
                  class="form-control"
                  id="Business Contact Number"
                  maxlength="12"
                  v-validate="'required|min:12'"
                  name="Business Contact Number"
                  :class="{'is-invalid': errors.has('Business Contact Number') }"
                  v-model="tempProduct.business_contact_number"
                  v-mask="'## #### ####'"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Business Contact Number')"
                >{{errors.first('Business Contact Number')}}</span>
              </div>

              <div class="col-sm-6 col-md-4 mb-4">
                <label for="ABN">ABN</label>
                <font color="red">*</font>
                <input
                  type="tel"
                  class="form-control"
                  id="ABN"
                  placeholder="ABN"
                  v-model="tempProduct.abn"
                  v-validate="'required|length:14'"
                  name="ABN"
                  :class="{'is-invalid': errors.has('ABN') }"
                  v-mask="'## ### ### ###'"
                >
                <span class="text-danger" v-if="errors.has('ABN')">{{errors.first('ABN')}}</span>
              </div>
            </div>
            <h3 class="my-3">DIRECTOR INFORMSTION</h3>
            <div class="form-row my-3">
              <div class="col-sm-6 col-md-4 mb-4">
                <label for="Proprietor">Name of Owner(s)/ Director(s)</label>
                <input
                  type="text"
                  class="form-control"
                  id="Proprietor"
                  name="Name of Owner(s)/ Director(s)"
                  :class="{'is-invalid': errors.has('Name of Owner(s)/ Director(s)') }"
                  v-model="tempProduct.director"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Name of Owner(s)/ Director(s)')"
                >{{errors.first('Name of Owner(s)/ Director(s)')}}</span>
              </div>

              <div class="col-sm-6 col-md-4 mb-4">
                <label for="Birth date">Date of Birth:</label>
                <el-date-picker
                  ref="picker"
                  :editable="true"
                  format="dd-MM-yyyy"
                  value-format="dd-MM-yyyy"
                  type="date"
                  placeholder="Date"
                  id="Birth date"
                  @change="dateChangebirthday"
                  class="w-100"
                  name="Birth date"
                  v-model="tempProduct.director_birth_date"
                ></el-date-picker>
                <span
                  class="text-danger"
                  v-if="errors.has('Birth date')"
                >{{errors.first('Birth date')}}</span>
              </div>
              <div class="col-sm-6 col-md-4 mb-4">
                <label for="Driving License No.">Driving License No.</label>
                <input
                  type="tel"
                  class="form-control"
                  id="Driving License No."
                  v-validate="'numeric'"
                  name="Driving License No."
                  :class="{'is-invalid': errors.has('Driving License No.') }"
                  v-model="tempProduct.driving_license_no"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Driving License No.')"
                >{{errors.first('Driving License No.')}}</span>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-6 mb-4">
                <label for="director_informstion_state">Issued State:</label>
                <select
                  class="custom-select mr-sm-2"
                  id="director_informstion_state"
                  v-model="tempProduct.director_state"
                >
                  <option selected value="0">NSW</option>
                  <option value="1">ACT</option>
                  <option value="2">WA</option>
                  <option value="3">QLD</option>
                  <option value="4">SA</option>
                  <option value="5">VIC</option>
                  <option value="6">TAS</option>
                </select>
              </div>
              <div class="col-sm-6 mb-4">
                <label for="Personal Address">Personal Address :</label>
                <input
                  type="text"
                  class="form-control"
                  id="Personal Address"
                  maxlength="4"
                  v-validate="'min:4'"
                  name="Personal Address"
                  :class="{'is-invalid': errors.has('Personal Address') }"
                  v-model="tempProduct.director_personal_address"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Personal Address')"
                >{{errors.first('Personal Address')}}</span>
              </div>
            </div>
            <h3 class="my-3">ACCOUNTS INFORMATION</h3>
            <div class="form-row mt-3">
              <div class="col-sm-6 col-md-4 mb-4">
                <label for="Contact Person">Accounts Contact Person</label>
                <input
                  type="text"
                  class="form-control"
                  id="Contact Person"
                  name="Accounts Contact Person"
                  v-model="tempProduct.accounts_contact_person"
                >
              </div>
              <div class="col-sm-6 col-md-4 mb-4">
                <label for="Accounts Contact Number">Accounts Contact Number</label>
                <input
                  type="tel"
                  class="form-control"
                  id="Accounts Contact Number"
                  maxlength="12"
                  v-validate="'length:12'"
                  name="Accounts Contact Number"
                  :class="{'is-invalid': errors.has('Accounts Contact Number') }"
                  v-model="tempProduct.accounts_contact_number"
                  v-mask="'## #### ####'"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Accounts Contact Number')"
                >{{errors.first('Accounts Contact Number')}}</span>
              </div>
              <div class="col-sm-12 col-md-4 mb-4">
                <label for="Accounts Contact Email">Accounts Contact Email</label>
                <input
                  type="email"
                  class="form-control"
                  id="Accounts Contact Email"
                  v-validate="'email'"
                  name="Accounts Contact Email"
                  :class="{'is-invalid': errors.has('Accounts Contact Email') }"
                  v-model="tempProduct.accounts_contact_email"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Accounts Contact Email')"
                >{{errors.first('Accounts Contact Email')}}</span>
              </div>
            </div>
            <h3 class="my-3">DELIVERY INFORMATION</h3>
            <div class="form-row my-3">
              <div class="col-sm-6 mb-4">
                <label for="Delivery Contact Person">Delivery Contact Person</label>
                <input
                  type="text"
                  class="form-control"
                  id="Delivery Contact Person"
                  name="Delivery Contact Person"
                  :class="{'is-invalid': errors.has('Delivery Contact Person') }"
                  v-model="tempProduct.delivery_contact_person"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Delivery Contact Person')"
                >{{errors.first('Delivery Contact Person')}}</span>
              </div>
              <div class="col-sm-6 mb-4">
                <label for="Delivery Contact Number">Delivery Contact Number</label>
                <input
                  type="tel"
                  class="form-control"
                  id="Delivery Contact Number"
                  maxlength="12"
                  v-validate="'min:12'"
                  name="Delivery Contact Number"
                  :class="{'is-invalid': errors.has('Delivery Contact Number') }"
                  v-model="tempProduct.delivery_contact_number"
                  v-mask="'## #### ####'"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Delivery Contact Number')"
                >{{errors.first('Delivery Contact Number')}}</span>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-4 mb-4">
                <label for="Delivery Address">Delivery Address</label>
                <input
                  type="text"
                  class="form-control"
                  id="Delivery Address"
                  name="Delivery Address"
                  :class="{'is-invalid': errors.has('Delivery Address') }"
                  v-model="tempProduct.delivery_contact_address"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Delivery Address')"
                >{{errors.first('Delivery Address')}}</span>
              </div>
              <div class="col-sm-4 mb-4">
                <label for="Delivery Address 2">Delivery Address 2</label>
                <input
                  type="text"
                  class="form-control"
                  id="Delivery Address 2"
                  name="Delivery Address 2"
                  :class="{'is-invalid': errors.has('Delivery Address 2') }"
                  v-model="tempProduct.delivery_contact_address_2"
                >
                <span
                  class="text-danger"
                  v-if="errors.has('Delivery Address 2')"
                >{{errors.first('Delivery Address 2')}}</span>
              </div>
              <div class="col-sm-4 mb-4">
                <label for="delivery_city">City</label>
                <input
                  type="text"
                  class="form-control"
                  id="delivery_city"
                  name="delivery_city"
                  v-model="tempProduct.delivery_contact_city"
                >
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-4 mb-4">
                <label for="delivery_postcode">Postcode</label>
                <input
                  type="text"
                  class="form-control"
                  id="delivery_postcode"
                  maxlength="4"
                  v-validate="'numeric|min:4'"
                  name="delivery_contact_postcode"
                  v-model="tempProduct.delivery_contact_postcode"
                >
              </div>
              <div class="col-sm-4 mb-4">
                <label for="delivery_state">State</label>
                <select
                  class="custom-select mr-sm-2"
                  id="delivery_state"
                  v-model="tempProduct.delivery_contact_state"
                >
                  <option selected value="0">NSW</option>
                  <option value="1">ACT</option>
                  <option value="2">WA</option>
                  <option value="3">QLD</option>
                  <option value="4">SA</option>
                  <option value="5">VIC</option>
                  <option value="6">TAS</option>
                </select>
              </div>
              <div class="col-sm-4 mb-4">
                <label for="delivery_country">Country</label>
                <input
                  type="text"
                  class="form-control"
                  id="delivery_country"
                  name="country"
                  v-model="tempProduct.delivery_contact_country"
                >
              </div>
            </div>
            <h3 class="my-3">Office/ Billing Address</h3>
            <div class="form-row">
              <div class="col-sm-4 mb-4">
                <label for="Office Address">Address</label>
                <input
                  type="text"
                  class="form-control"
                  id="Office Address"
                  name="Address"
                  v-model="tempProduct.office_address"
                >
              </div>
              <div class="col-sm-4 mb-4">
                <label for="Office Address 2">Address 2</label>
                <input
                  type="text"
                  class="form-control"
                  id="Office Address 2"
                  name="Address 2"
                  v-model="tempProduct.office_address_2"
                >
              </div>

              <div class="col-sm-4 mb-4">
                <label for="Office city">City</label>
                <input
                  type="text"
                  class="form-control"
                  id="Office city"
                  name="Office city"
                  v-model="tempProduct.office_city"
                >
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-4 mb-4">
                <label for="Office postcode">Postcode</label>
                <input
                  type="text"
                  class="form-control"
                  id="Office postcode"
                  maxlength="4"
                  v-validate="'numeric|min:4'"
                  name="postcode"
                  :class="{'is-invalid': errors.has('postcode') }"
                  v-model="tempProduct.office_postcode"
                >
                <span class="text-danger" v-if="errors.has('postcode')">{{errors.first('postcode')}}</span>
              </div>
              <div class="col-sm-4 mb-4">
                <label for="Office state">State</label>
                <select
                  class="custom-select mr-sm-2"
                  id="Office state"
                  v-model="tempProduct.office_state"
                >
                  <option selected value="0">NSW</option>
                  <option value="1">ACT</option>
                  <option value="2">WA</option>
                  <option value="3">QLD</option>
                  <option value="4">SA</option>
                  <option value="5">VIC</option>
                  <option value="6">TAS</option>
                </select>
              </div>
              <div class="col-sm-4 mb-4">
                <label for="Office country">Country</label>
                <input
                  type="text"
                  class="form-control"
                  id="Office country"
                  name="Office country"
                  v-model="tempProduct.office_country"
                >
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">Cancel</button>
            <button type="button" class="btn btn-primary">Archive</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueCountUp from "vue-countupjs";
import $ from "jquery";
export default {
  VueCountUp,
  data() {
    return {
      start: 1,
      all: 32200,
      general: 15000,
      business: 17200,
      tempProduct: {},
      columns: [
        "id",
        "first_name",
        "mobile_number",
        "email",
        "abn",
        "postcode",
        "payment",
        "Edit"
      ],
      tableData: [],
      options: {
        headings: {
          id: "#",
          first_name: "User",
          mobile_number: "Mobile Number",
          email: "Email",
          abn: "ABN",
          postcode: "Postcode"
        }
      }
    };
  },
  mounted() {
    //table-2 抓回資料
    const vm = this;
    vm.$store.dispatch("updateLoading", true);
    this.$http.get("http://api.localhost/api/admin/user/list").then(res => {
      this.tableData = res.data.list;
      this.tempProduct = res.data.list;
      vm.$store.dispatch("updateLoading", false);
    });
  },
  methods: {
    //日期
    dateChangebirthday(val) {
      console.log(val);
      this.tempProduct.birth = val;
    },
    //修改
    edit(item) {
      const vm = this;
      this.tempProduct = Object.assign({}, item);
      console.log(item);
      console.log(this.tempProduct);
      $("#userModal").modal("show");
    },

    //Excel匯出
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    export2Excel() {
      let that = this;
      require.ensure([], () => {
        const {
          export_json_to_excel
        } = require("../../vendor/Export2Excel.js");
        const tHeader = [
          that.columns[0],
          that.columns[1],
          that.columns[2],
          that.columns[3],
          that.columns[4],
          that.columns[5],
          that.columns[6]
        ];
        const filterVal = [
          that.columns[0],
          that.columns[1],
          that.columns[2],
          that.columns[3],
          that.columns[4],
          that.columns[5],
          that.columns[6]
        ];
        const list = this.tableData;
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "User");
      });
    }
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    }
  }
};
</script>
